@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition-property:background , color , box-shadow ;
  transition-duration:  0.4s;
}
body {
  font-family: "Poppins", "sans-serif", "Gill Sans", "Gill Sans MT", Calibri,
    "Trebuchet MS", sans-serif;
    overflow-x: hidden;
}
.container{
  padding:0 5%;
  width: 100vw;
 margin: 0 auto;
}

.switched{
  -webkit-transform: translateX(15px) !important;
          transform: translateX(15px) !important;
  background: hsl(209, 23%, 22%) !important;
}
.switched-parent{
  background: #fafafa !important;
}
.dropped{
  -webkit-transform:scale(1) translateY(0%) !important;
          transform:scale(1) translateY(0%) !important;
  opacity: 1 !important;
  -webkit-clip-path: circle(100%) !important;
          clip-path: circle(100%) !important;
  transition: clip .1s , opacity 0.6s , -webkit-transform 0.5s;
  transition: transform 0.5s , clip .1s , opacity 0.6s;
  transition: transform 0.5s , clip .1s , opacity 0.6s , -webkit-transform 0.5s;

}
@-webkit-keyframes switch {
  to{
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
    background: burlywood;

  }
}
@keyframes switch {
  to{
    -webkit-transform: translateX(15px);
            transform: translateX(15px);
    background: burlywood;

  }
}
::-webkit-scrollbar{
  /* display: none; */
  background: rgba(104, 103, 103, 0);  width: 4px;


}
::-webkit-scrollbar-thumb{
  background: rgb(136, 45, 255);
  width: 1px;

}
::-webkit-scrollbar-track{
  background: rgba(255, 255, 255, 0)
}


